.color-nav {
    background-color: #091717;
}

.navbar {
    padding-top: 0px;
    padding-bottom: 0px;
}

.media-body-align {
    text-align: left;
}

.about-content {
    font-size: large;
}

.profile-img {
    height: 360px;
    width: 360px;
}

@media screen and (max-width: 360px) {
    .profile-img {
        height: 200px;
        width: 200px;
    }
}

.about-container {
    text-align: center;
    padding-top: 60px;
    padding-bottom: 10rem;
    /* height: 100vh; */
    background-color: #FBF9F9;
}

.section-header {
    text-align: center;
    padding-bottom: 50px;
}

.skill-container {
    padding-bottom: 10rem;
    padding-top: 10rem;
}


@media screen and (max-width: 992px) {
    .card-container {
        padding-bottom: 50px;
        text-align: -webkit-center;
    }
}

@media screen and (max-width: 768px) {
    .sub-contact-container {
        padding-bottom: 50px;
        text-align: -webkit-center;
    }
}

@media screen and (min-width: 768px) {
    .padding-skills-right {
        padding-right: 50px;
    }
    .padding-skills-left {
        padding-left: 50px;
    }
}

@media screen and (max-width: 1024px) {
    .education-sub-section {
        padding-right: 30px;
        padding-left: 30px;
        font-size: larger;
    }
}

.education-sub-header {
    font-size: 1.5rem;
}

.coursework-header {
    font-size: 1.36rem;
}

@media screen and (max-width: 768px) {
    .padding-coursework {
        padding-bottom: 50px;
    }
}


.skills-img {
    height: 90px;
    width: 90px;
    padding-bottom: 10px;
}

.card-container {
    text-align: -webkit-center;
}

.card {
    background-color: #EDEDED;
    border: 0;
    border-radius: 2.25rem;
}

.card-style {
    width: 75%;
}


.card-img-top {
    height: 200px;
}

.experience-container {
    background-color: #FBF9F9;
    padding-bottom: 10rem;
    padding-top: 10rem;
}

.education-container {
    padding-bottom: 10rem;
    padding-top: 10rem;
    text-align: center;
}

.contact-container {
    /* padding-bottom: 10rem; */
    padding-top: 10rem;
    text-align: center;
    background-color: #FBF9F9;
}

.nav-highlight {
    font-weight: bolder;
}

.contact-logos {
    height: 50px;
    width: 50px;
}

.contact-links {
    color: black
}

.copyright-color {
    color: #D1D1D1;
}
